html, body { width: 100vw; height: 100vh; overflow: hidden; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html { width: 100%; height: 100%; }

body {
  background: #000;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#timer {
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #fff;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#syncstat {
  position: absolute;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
}

.transition {
  transition: 0.5s;
}
.overlay { z-index: 2; background: #0008; display: flex; justify-content: center; align-items: center; transition: 0.5s; position: fixed; width: 100vw; height: 100vh; top: 0; left: 0; }
.overlay_visible { opacity: 1; cursor: auto; }
.overlay_hidden { opacity: 0; cursor: none; }
.overlay-close { margin-top: 10px; width: 100%; height: 3rem; background: #000; border: 2px solid white; font-size: 24px; color: #fff; }

.redtext { color: #f00; }
.timeSetter { display: flex; flex-direction: column; align-items: center; }
.newbg, .newbgi, .timeSetter, .tmrdsp { height: 303px; width: 280px; border: 2px solid white; }

.TS-opt { width: 260px; height: 251px; font-size: 24px; background: #0008; padding: 10px; color: #fff; display: flex; align-items: center; flex-direction: column; }
.TS-uparrow { width: 0; height: 0; border-right: 25px solid transparent; border-left: 25px solid transparent; border-bottom: 25px solid white; cursor: pointer; }
.TS-downarrow { width: 0; height: 0; border-right: 25px solid transparent; border-left: 25px solid transparent; border-top: 25px solid white; cursor: pointer; }
.TS-timesel { font-size: 62px; margin: 10px 0px; display: flex; flex-direction: row; justify-content: center; }
.TS-sel { display: flex; flex-direction: column; align-items: center; justify-content: center; margin: 0px 5px; }
.TS-selsplitter { -webkit-transform: translateY(20px); transform: translateY(20px); }
.TS-submit { background: #000; border: 2px solid white; font-size: 24px; color: #fff; cursor: pointer; width: 100%; }
.TS-day_select { background: #0008; width: 100%; height: 32px; display: flex; align-items: center; }
.TS-day_sub, .TS-day_add { text-align: center; width: 55px; -webkit-user-select: none; -ms-user-select: none; user-select: none; font-weight: bold; cursor: pointer; }
.TS-day_indicator { flex-grow: 1; text-align: center; }

.arrow-margin-left { margin-right: 15px; }
.arrow-margin-right { margin-left: 15px; }

.control-titlebox { background: #000; color: #fff; font-size: 24px; width: 280px; height: 2rem; display: flex; align-items: center; justify-content: center; }
.controls-norm { height: 307px; display: flex; flex-direction: row; }
.controls-tall { height: 370px; }
.controls-arrow { width: 6rem; height: 100%; background: #0008; display: flex; justify-content: center; align-items: center; }
.controls-RA-active { border-top: 150px solid transparent; border-bottom: 150px solid transparent; border-left: 50px solid white; cursor: pointer; }
.controls-LA-active { border-top: 150px solid transparent; border-bottom: 150px solid transparent; border-right: 50px solid white; cursor: pointer; }
.controls-RA-inactive { border-top: 150px solid transparent; border-bottom: 150px solid transparent; border-left: 50px solid #454545; }
.controls-LA-inactive { border-top: 150px solid transparent; border-bottom: 150px solid transparent; border-right: 50px solid #454545; }

.panel-controls-short { width: 100%; height: 3rem; margin-bottom: 15px; display: flex; flex-direction: row; }
.panel-controls-short-arrowspace-L { display: flex; justify-content: center; align-items: center; margin-right: 5px; flex-grow: 1; height: 100%; background: #0008; }
.panel-controls-short-arrowspace-R { display: flex; justify-content: center; align-items: center; margin-left: 5px; flex-grow: 1; height: 100%; background: #0008; }
.panel-controls-short-arrow-L-active { border-top: 15px solid transparent; border-bottom: 15px solid transparent; border-right: 45px solid white; }
.panel-controls-short-arrow-R-active { border-top: 15px solid transparent; border-bottom: 15px solid transparent; border-left: 45px solid white; }
.panel-controls-short-arrow-L-inactive { border-top: 15px solid transparent; border-bottom: 15px solid transparent; border-right: 45px solid #454545; }
.panel-controls-short-arrow-R-inactive { border-top: 15px solid transparent; border-bottom: 15px solid transparent; border-left: 45px solid #454545; }

.overlay-controls-container { display: flex; flex-direction: column; height: 100%; }
.overlay-closespace { flex-grow: 1; }
.overlay-container { display: flex; justify-content: center; width: 100%; height: 100%; }

.newbg_solid_select input { width: 100px; margin: 10px 0; }
.newbg_solid_select { display: flex; flex-direction: column; align-items: center; flex-grow: 1; }
.newbg_solid_indicator { color: #fff; font-size: 32px; display: flex; justify-content: center; align-items: center; height: 125px; width: 200px; border: 2px solid white; flex-grow: 1; }
.newbg_solid_textbox { background: #000; color: #fff; border: 2px solid white; font-size: 14pt; }
.newbg_indicator_fail { background: #000; height: 125px; width: 200px; border: 2px solid white; display: flex; justify-content: center; align-items: center; flex-direction: column; }
.newbg_indicator_explain { text-align: center; font-size: 14px; }
.newbg-submit { background: #000; border: 2px solid white; font-size: 24px; margin-top: 5px; color: #fff; cursor: pointer; width: 100%; }
.newbg_image_select { display: flex; flex-direction: column; flex-grow: 1; align-items: center; }
.newbg_image_textbox { margin: 10px 0; font-size: 14pt; background: #000; border: 2px solid white; color: #fff; }
.newbg_image_textbox_container { display: flex; flex-direction: column; }
.newbg_image_container { overflow: hidden; width: 200px; height: 93px; border: 2px solid white; background: #000; }
.newbg_image_preview { width: 100%; }

.newbgi-fileinfo { display: flex; flex-direction: column; background: #0008; padding: 10px; width: 240px; overflow-x: hidden; flex-grow: 1; }
.newbgi-fileinfo span { color: #fff; text-align: left; -webkit-user-select: auto; -ms-user-select: auto; user-select: auto; cursor: text; }
.newbgi-container { display: flex; flex-direction: column; height: 100%; }
.newbgi-item { margin: 7.5px 0px; }
.newbgi-upload { background: #000; color: #fff; border: 2px solid white; font-size: 24px; cursor: pointer; width: 100%; }
.newbgi-fithoriz { margin-bottom: 15px; }

.tmrdsp_options { flex-grow: 1; width: 260px; }
.tmrdsp_options span { font-weight: bold; font-size: 24px; margin-right: 5px; }
.tmrdsp_fcolr, .tmrdsp_fsize, .tmrdsp_etxt, .tmrdsp_ttxt { background: #000; border: 2px solid white; color: #fff; font-weight: bold; font-size: 24px; }
.tmrdsp_fcolr { width: 90px; }
.tmrdsp_fsize { width: 50px; }
.tmrdsp_etxt, .tmrdsp_ttxt { width: 252px; }
.tmrdsp_submit { width: 100%; background-color: #000; border: 2px solid white; font-size: 24px; }
.tmrdsp-s-enabled-complete { color: #0f0; }
.tmrdsp-s-enabled { color: #fff; }
.tmrdsp-s-disabled { color: #f00; }
.tmrdsp_fcol_cont, .tmrdsp_fsiz_cont { display: flex; flex-direction: row; }
.tmrdsp_filler { flex-grow: 1; }
.tmrdsp_span-color { display: flex; flex-direction: row; }
.tmrdsp_ttxt_colr, .tmrdsp_etxt_colr { width: 90px; border: 2px solid white; background: #000; color: #fff; font-weight: bold; font-size: 24px; }
.tmrdsp_etxt_cont, .tmrdsp_ttxt_cont { display: flex; flex-direction: column; }
.background {
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.background_image-horizontal { width: 100%; }
.background_image-vertical { height: 100%; }
.neversleep {
    z-index: -2;
    position: absolute;
    top: 0;
    left: 0;
}
.title_cont {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
}

.title_text {
    color: #fff;
    font-size: 36px;
    font-weight: bold;
    text-align: center;
}
